import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root"
})
export class TaskServiceService {
  constructor(private http: HttpClient) {}

  getTasks(): Observable<Task[]> {
    return this.http.post<Task[]>("/fetchAllTasks", 1996);
  }

  searchTasks(content: String): Observable<Task[]> {
    return this.http.post<Task[]>("/searchTask", content);
  }

  getPendingTasks(): Observable<Task[]> {
    return this.http.post<Task[]>("/fetchAllIncompleteTasks", 1996);
  }

  addTasks(t: Task): Observable<boolean> {
    return this.http.post<boolean>("/saveTask", t);
  }

  changeStatusOfTask(id: number): Observable<boolean> {
    return this.http.post<boolean>("/changeStatus", id);
  }
}
export interface Task {
  taskId: number;
  userId: number;
  content: String;
  status: String;
}
